import React from 'react';
import Tag from './Tag';
import { ifDoc } from '../../utils/browser';

function mount(element) {
  ifDoc(document => document.body.appendChild(element));
}

function unmount(element) {
  element.parentNode.removeChild(element);
}

export default function Script(props) {
  return (<Tag {...props} name="script" mount={mount} unmount={unmount} />);
}



