export const mockEventTarget = {
  addEventListener() {

  },
  removeEventListener() {

  }
};

export function ifDoc(callback) {
  if (typeof document !== 'undefined') {
    callback(document);
  }
}

export const doc = typeof document === 'undefined' ? mockEventTarget : document;
export const win = typeof window === 'undefined' ? mockEventTarget : window;
export const nav = typeof navigator === 'undefined' ? mockEventTarget : navigator;


